.rubik-header {
  font-family: "Rubik", sans-serif;
  font-optical-sizing: auto;
  font-weight: 500;
  font-style: normal;
}


.image-container {
  position: relative;
  overflow: hidden;
  border-radius: 50%;
  width: 100%;
  height: auto;
}

.image-container img {
  width: 100%;
  height: auto;
  transition: transform 0.5s ease;
}

.image-container:hover img {
  transform: scale(1.1);
}

.image-container::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background: rgba(0, 0, 0, 0.3);
  transition: background 0.5s ease;
}

.image-container:hover::after {
  background: rgba(0, 0, 0, 0.1);
}

.icon-effect {
  transition: transform 0.2s ease-in-out;
}
.icon-effect:hover {
  transform: scale(1.1);
}


/* Projects.css */

.card {
  transition: transform 0.3s ease-in-out, box-shadow 0.3s ease-in-out;
  border: none;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  height: 100%;
}

.card:hover {
  transform: scale(1.05);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.3);
}

.card-img-top {
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  height: 200px;
  object-fit: cover;
  object-position: 0% 0%;
}

.project-card {
  background: rgba(255, 255, 255, 0.1);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.card-body {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}

.card-text {
  flex-grow: 1;
}

.btn {
  align-self: center;
}

.btn-cyan {
  background-color: #0e0e0e;
  color: white;
  border: none;
}

.btn-cyan:hover {
  background-color: white;
  color: #0e0e0e;
}






